














































import mapView from '@/pages/zhandianMsg/map';

var view
export default {
	components: {
		mapView
	},
	data() {
		return {
			index: 0,
			siteName: '',
			superviseList: []
		};
	},
	created: function() {
		view = this;
	},
	mounted: function() {
		this.getList()
	},
	watch: {
		siteName: function() {
			this.getList()
		}
	},
	methods: {
		gotodetail: function(item) {
			uni.setStorageSync('zhandianMsgID', item.id);
			uni.navigateTo({
				url: '../zhandianDetails/zhandianDetails'
			})
			console.log(item)
		},
		getList: function() {
			var msg = {
				siteName: this.siteName,
				areaId: '',
				streetId: '',
				companyId: ''
			}
			this.$request({
				url: 'findSiteList',
				method: 'post',
				showLoading:true,
				loadingText:'加载中',
				data: msg,
				success: (jsonData) => {
					console.log(777766)
					console.log(jsonData)
					if (jsonData.status) {
						this.superviseList = jsonData.data
					} else {
						uni.showToast({
							icon: 'none',
							title: jsonData.msg,
							duration: 1000
						});
					}
				},
				complete() {
					view.loading = false;
				}
			});
		},
	}
}
